@media (max-width: 768px) {
  .burger {
    position: absolute;
    right: 15px;
    top: 25px;
    z-index: 5;
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .burger span {
    position: absolute;
    left: 0;
    top: calc(50% - 1px);
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s ease 0s;
  }
  .burger span:first-child {
    top: 0;
  }
  .burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .burger._active span {
    transform: scale(0);
  }
  .burger._active span:first-child {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
  }
  .burger._active span:last-child {
    bottom: calc(50% - 1px);
    transform: rotate(45deg);
  }
}

